import React from 'react';
import styled from 'styled-components';

const Nav = styled.nav`
  min-width: 70%;
`

const List = styled.ul`
    display: flex;
     justify-content: space-between;
    list-style-type: none;
    
`

const Link = styled.li`
    font-size: 18px;
`

const LinkA = styled.a`
    font-size: 18px;
    text-decoration: none;
   color: #000;
`

const Navigation = () => {
    return (
        <Nav>
            <List>
                <Link>
                    <LinkA href='#'></LinkA>
                </Link>
                <Link>
                    <LinkA href='#'></LinkA>
                </Link>
                <Link>
                    <LinkA href='#'></LinkA>
                </Link>
                <Link>
                    <LinkA href='#'></LinkA>
                </Link>
                <Link>
                    <LinkA href='#'></LinkA>
                </Link>
                {/*<Link></Link>
                <Link></Link>
                <Link></Link>
                <Link></Link>
                <Link></Link>*/}
            </List>
        </Nav>
    );
};

export default Navigation;